<template>
  <div class="frame-page">
    <!-- icon name cross -->
    <!-- <Header :leftArrow="!isIos" :leftClick="back"><van-icon name="arrow-left" slot="left" color="#000"/></Header> -->
    <iframe :src="src" frameborder="0" style="width: 100%; height: 100%;"></iframe>
  </div>
</template>

<script>
// import Header from '@/components/header'

import UplusApi from '@uplus/uplus-api';
import config from '@/config'
const instance = new UplusApi();
instance.initDeviceReady()

export default {
  components: {
    // Header
  },
  data() {
    return {
      src: '',
      isIos: this.$util.isIos()
    }
  },
  mounted() {
    if (this.$util.isIos()) {
      let orderSn = this.$route.query.orderSn
      sessionStorage.setItem('orderSn', orderSn);
      console.log(orderSn, 'orderSn')
    }
    this.src = config.HOST + '/sgmobile/iframe'
    console.log(this.src, 'iframe src')
    if (this.$util.isAndroid()) {
      instance.initDeviceReady().then(() => {
        instance.androidPhysicalBtForBack({
          process: true,
          deviceChangeListener: () => {
            console.log('process为true，android物理返回动作时触发,在此执行相应逻辑');
            // instance.closeH5ContainerView();
            this.$router.back()
          },
          eventKey: 'backEvent'
        });
      })
    }
  },
  methods: {
    back() {
      if (this.isIos) return
      console.log('back')
      this.$router.back()
      // instance.initDeviceReady().then(() => {
      //   instance.closeH5ContainerView();
      // });
    }
  }
}
</script>

<style>
.frame-page{
  /* padding-top: .88rem; */
  height: 100%;
  box-sizing: border-box;
}
</style>
